import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../../../../auth/_services/product.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
import { DataService } from '../../../../../_services/data.service';
import { FormGroup, FormBuilder, Validators,ReactiveFormsModule } from '@angular/forms';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
@Component({
    selector: 'app-attribute-list',
    templateUrl: "attribute-list.component.html",
    styles: []
})
export class AttributeListComponent implements OnInit {


    form: FormGroup;
    private formSumitAttempt: boolean;

    constructor(private toastr: ToastrService, private route: ActivatedRoute, private router: Router, private formBuilder: FormBuilder, private _dataService: DataService, private _productService: ProductService) { }
    public attrData;
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public userData;
    public attributeData = '';
    public product_id = '';
    public selectedStatus = '';
    public user_id = '';
    public name = '';
    public btn_text = 'Save';
    public form_title = 'Add New';
    public id = '';
    public filterValue = '';
    public filterDataHolder: any;
    p: number = 1;
   
    currentUser: any;
    ngOnInit() {

        this.form = this.formBuilder.group({
            id: [null],
            name: [null,Validators.required],
        });


        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));

        this.user_id = this.currentUser.success.id;


       /* this.route.params.subscribe(
            params => {*/
                //if (params.product_id) {
                    this._productService.getAttributes().subscribe(
                        data => {
                            //console.log('ss');
                            this.attrData = data.success;
                            this.noOfPage = data.success.length;

                        }
                    );
               // }
          //  }
       // );


    }


    

    // onSubmitAddAttr() {
    //         let input: any;
    //         this.formSumitAttemptAttr = true;
    //         if (this.formAttr.valid) {
    //             input = { form_data: this.formAttr.value };
    //             input.form_data.fk_user_id = this.currentUser.success.id;
    //             input.form_data.fk_product_id = this.product_id;
    //             // console.log(input);
    //             this._productService.addAttribute(input).subscribe(
    //                 data => {
    //                     this.reset();
    //                 }
    //             );
    //         }
    //     }

    onSubmitAddAttr() {
        this.formSumitAttempt = true;
        let input: any;
       

            input = { form_data: this.form.value };
            console.log(input.form_data.id);
            if (this.form.valid) {
                if(input.form_data.id == null || input.form_data.id == 0){

                    this._productService.addNewAttribute(input).subscribe(
                    data => {
                        //this.attributeData = data.success;
                        this.toastr.success('Product Attribute Added Successfully', 'Success Message');
                        this.attributeData = data.data;
                        // that.form.c;
                        this._productService.getAttributes().subscribe(
                            data => {
                                this.attrData = data.success;
                                this.noOfPage = data.success.length;

                            }
                        );
                        this.reset();
                    }
                    );

                   
                }else{
                     this._productService.updateNewAttribute(input).subscribe(
                        data => {
                            //this.attributeData = data.success;
                            this.toastr.success('Product Attribute Updated Successfully', 'Success Message');
                            this.attributeData = data.data;
                            // that.form.c;
                            this._productService.getAttributes().subscribe(
                                data => {
                                    this.attrData = data.success;
                                    this.noOfPage = data.success.length;

                                }
                            );
                            this.reset();
                            this.btn_text = "Add";
                            this.form_title = "Add New";
                        }
                    );
                }
                
            }
                

    }


    getAttrList(event) {
        console.log(event.target.value);
        this._dataService.setProductState(event.target.value);
        this._dataService.getProductState().subscribe(
            data => {
                // this.hide = data;
            }
        );
    }

    getAttrByID(att_id) {
        this._productService.getAttributeListsById(att_id).subscribe(
            data => {
               
                this.name = data.success.name;
                this.id = data.success.id;
                this.btn_text = "Update";
                this.form_title = "Update Attribute";
            }
        );
    }

    deleteNewAttribute(att_id) {
        let value = confirm('Are you want to delete this attribute ?');
        if (value) {
            this._productService.deleteNewAttribute(att_id).subscribe(
                data => {
                    this.toastr.success('Product Attribute Deleted Successfully', 'Success Message');
                    this._productService.getAttributes().subscribe(
                                data => {
                                    this.attrData = data.success;
                                    this.noOfPage = data.success.length;

                                }
                    );
                }
            );
        }
    }


    reset() {
       // if (this.selectedStatus != '') {
            this.form.reset();
            this.selectedStatus = '';
            this.formSumitAttempt = false;
       // }
    }


    filterByValue(){
       
        let attr_value_data = [];
        if (this.filterValue == '') {
            this.attrData = this.filterDataHolder;
        } else {
            if (this.filterDataHolder == undefined) {
                this.filterDataHolder = this.attrData;
            } else {
                this.attrData = this.filterDataHolder;
            }
            
            this.attrData.forEach(obj => {
                //console.log(obj);
               if (obj.name != '' && obj.name != null && obj.name != undefined) {
                    if (obj.name.replace(/<[^>]*>/g, "").toUpperCase().indexOf(this.filterValue.toUpperCase()) > -1) {
                      
                        attr_value_data.push(obj);
                    }
                }
           });
            this.attrData = attr_value_data;
        } 
    }

    public attrSearch() {
        this._productService.attrSearching({ search_value: this.searchValue, dropdown_value: this.dropDownValue, user_id: this.currentUser.success.id }).subscribe(
            data => {
                this.attrData = data.success;
            }
        );
    }
    public attribute_delete(del_id) {

        console.log(del_id);
        let value = confirm('Are you want to delete this attribute ?');
        if (value) {
            this._productService.deleteAttribute(del_id).subscribe(
                data => {
                    this.attrData = data.data;
                }
            );
        }

    }
    public changeStatus(attr: any) {
        if (attr.status == 'Active') {
            attr.status = "Inactive";

            this._productService.updateAttrStatus(attr.id).subscribe(

                data => {
                    console.log('update', data);
                }
            );
        } else {
            attr.status = 'Inactive';
            this._productService.updateAttrStatus(attr.id).subscribe(

                data => {
                    console.log('update', data);
                }
            );
        }
    }
}
